import "../scss/style.scss";
// -----------------------------------------Next-----------------------------------------
import Head from "next/head";
import Link from "next/link";
// -----------------------------------------Reactstrap-----------------------------------------
import { Container, Row, Col, Button } from "reactstrap";
// -----------------------------------------React-----------------------------------------
import React from "react";
// -----------------------------------------Components-----------------------------------------
import CenteredHeading from "../components/CenteredHeading"
import Document from "../layouts/document";
// -----------------------------------------Resources-----------------------------------------
import Image from "../images/hero-bg-2.jpg"

export default () => (
	<Document>
		<Row>
			<Col>
				<CenteredHeading picture={ Image } >
					<div className="headers">
						Welcome to the landing page
					</div>
				</CenteredHeading>
			</Col>
		</Row>
		<Row>
			<Col>
				<div class="container-fluid landing-page">
					{/* <!--  Replace logo image with logo svg  --> */ }
					<img src="http://cordine.site/static/media/christopher-logo-white.427e237a.png" class="logo" />
					<div class="row hero">
						<div class="centerContainer">
							<h1 class="quickSand"><span style={ { color: "white" } }>New York</span> Web Developer</h1>
							{/* <!--    On click, add removable tag to search of item clicked, these will be font awesome svgs, with an alt text of the text below    -->
      <p>React, Express, MongoDB, Node, Next, Redux, MySQL, Linux, PHP, Git, Bash, Magento, Wordpress, Bootstrap, Sass, etc...</p>
								<!--    Use search to immediately display related work    --> */}
							<div class="search-container"><input type="search" placeholder="What are you looking for?" /></div>
							<p class="subsearch">Use the search bar above to immediately find <a href="#">related work</a> I've done, or scroll down to view my featured projects and front end design.</p>
							{/* <!--    Enter short links here, just icons with hover text    --> */ }
							<p>GitHub, Codepen, Resume, Projects</p>
						</div>
						<div class="hero-bg"></div>

					</div>
				</div></Col>
		</Row>
	</Document>
);
